#smallComponenetDate {
  font-weight: normal;
  font-size: 13px;
  line-height: 17px;
}
#smallComponentAuthor {
  font-weight: normal;
  font-size: 13px;
  line-height: 17px;
}
#readStory {
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
}
.smallImageComp {
  /* width: 360px; */
  width: 337px;
  height: 500px;
  color: white;
  position: relative;
  cursor: pointer;
}
.smallImageComp:hover {
  position: relative;
  bottom: 30px;
}
#secondSmallComp {
  position: absolute;
  top: 444px;
  left: 40px;
  display: flex;
  align-items: center;
}
#readStory {
  margin-right: 14px;
}
#crossLine {
  height: 1px;
  background: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.25;
  position: absolute;
  top: 423px;
  left: 40px;
}

@media (max-width: 1349px) {
  .smallImageComp {
    width: 256px;
  }
}

@media (max-width: 1089px) {
  .smallImageComp {
    width: 384px;
  }
}

@media (max-width: 1024px) {
  .smallImageComp {
    width: 256px;
  }
}
/* tablet size */
@media (max-width: 1001px) {
  /* .smallImageComp was here for 384px as width */
}

@media (max-width: 768px) {
  .smallImageComp {
    width: 384px;
  }
}

/* mobile size */
@media (max-width: 765px) {
  .smallImageComp {
    width: 100%;
  }
}
