#introduction {
  height: 650px;
  color: white;
  display: flex;
  align-items: center;
  background: url("../assets/stories/desktop/moon-of-appalacia.jpg");
}
#featuredStory {
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  width: 242px;
  height: 16px;
}
#fullMoon {
  font-weight: bold;
  font-size: 40px;
  line-height: 48px;
  letter-spacing: 4.16667px;
  text-transform: uppercase;
  width: 387px;
  height: 144px;
  margin-top: 24px;
  margin-bottom: 16px;
}
#date {
  font-weight: normal;
  font-size: 13px;
  line-height: 17px;
  mix-blend-mode: normal;
  opacity: 0.75;
  width: 100px;
  height: 17px;
  margin-right: 9px;
}
#author {
  font-weight: normal;
  font-size: 13px;
  line-height: 17px;
  width: 116px;
  height: 17px;
}
#storiesDescription {
  font-weight: normal;
  font-size: 15px;
  line-height: 25px;
  mix-blend-mode: normal;
  opacity: 0.6;
  width: 387px;
  height: 125px;
  margin: 24px 0;
}
#readStories {
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  text-align: right;
  letter-spacing: 2px;
  margin-right: 14px;
  cursor: pointer;
}
#readStories:hover {
  text-decoration: underline;
}
#storiesContent {
  display: grid;
  /* we don't really need to specify the number of rows. we only need
  the number of columns */
  grid-template-columns: repeat(4, 1fr);
}
#smallComponentHeading {
  margin-top: 8px;
}
#firstSmallComp {
  top: 330px;
}
/* used to be 1089px */
@media (max-width: 1024px) {
  /* #introduction {
    background: url("../assets/stories/tablet/moon-of-appalacia.jpg");
  } */
  /* #storiesContent {
    grid-template-columns: repeat(3, 1fr);
  } */
}

@media (max-width: 768px) {
  #introduction {
    background: url("../assets/stories/tablet/moon-of-appalacia.jpg");
  }
  #storiesContent {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 590px) {
  #featuredStory,
  #fullMoon,
  #storiesDescription {
    width: 100%;
  }
}

@media (max-width: 500px) {
  #storiesContent {
    display: block;
  }
}
