#pricingIntro {
  height: 490px;
  display: flex;
  background-color: black;
  color: white;
  flex-direction: row-reverse;
}
#pricingIntro span {
  width: 610px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#pricingIntro img {
  width: 63%;
}

#pricingComponent h2 {
  font-weight: bold;
  font-size: 40px;
  line-height: 48px;
  letter-spacing: 4.16667px;
  text-transform: uppercase;
}

#pricingComponent div {
  font-weight: normal;
  font-size: 15px;
  line-height: 25px;
  mix-blend-mode: normal;
  opacity: 0.6;
  width: 387px;
  height: 75px;
  margin-top: 21px;
}
#pricingInviteComp .container {
  display: flex;
  justify-content: space-between;
}

#pricingInviteComp h2 {
  font-weight: bold;
  font-size: 40px;
  line-height: 48px;
  letter-spacing: 4.16667px;
  text-transform: uppercase;
  width: 400px;
}
#pricingInviteComp span {
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  text-align: right;
  letter-spacing: 2px;
  margin-right: 14px;
  cursor: pointer;
}
#pricingInviteComp span:hover {
  text-decoration: underline;
}
#tableComponent div {
  font-weight: bold;
  font-size: 40px;
  line-height: 48px;
  text-align: center;
  letter-spacing: 4.16667px;
  text-transform: uppercase;
}
#pricingFeatures {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 48px;
  margin-bottom: 160px;
}
#gridComponent {
  margin-top: 120px;
}
/* Switch component from material UI styling */

.MuiSwitch-thumb {
  color: black;
}
.dateTimes {
  font-weight: bold;
  font-size: 18px;
  line-height: 25px;
}

@media (max-width: 1032px) {
  #pricingIntro img {
    width: 600px;
  }
}

@media (max-width: 987px) {
  #pricingIntro img {
    content: url("../assets/pricing/tablet/hero.jpg");
  }
  #pricingIntro span {
    padding: 0 40px;
  }
  #pricingFeatures {
    flex-direction: column;
  }
}
@media (max-width: 740px) {
  #pricingIntro {
    display: block;
    height: 600px;
  }
  #pricingIntro img {
    content: url("../assets/pricing/mobile/hero.jpg");
    width: 100%;
    height: 350px;
  }
  #pricingComponent {
    margin-top: 50px;
  }
  #pricingInviteComp .container {
    display: block;
    text-align: center;
  }
  #pricingInviteComp .container h2 {
    width: 100%;
    margin-bottom: 20px;
  }
  #pricingInviteComp .container div {
    justify-content: center;
  }
}
@media (max-width: 414px) {
  #pricingIntro span,
  #pricingComponent div {
    width: 100%;
  }
}
