#footerComponent {
  height: 250px;
  background-color: black;
  color: white;
  display: flex;
  align-items: center;
}

.footerLinks {
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  text-transform: uppercase;
  cursor: pointer;
  color: white;
}
.footerLinks:hover {
  color: white;
  opacity: 0.3;
}
#getInvite {
  display: flex;
  align-items: center;
}
#getInvite span {
  margin-right: 14px;
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  text-align: right;
  letter-spacing: 2px;
  cursor: pointer;
}
#getInvite span:hover {
  text-decoration: underline;
}
#copyRight {
  font-weight: normal;
  font-size: 15px;
  line-height: 20px;
  mix-blend-mode: normal;
  opacity: 0.5;
  width: 255px;
}
#secondSocialLink,
#fourthSocialLink {
  margin: 0 10px;
}

#footerComponent .container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 112px;
}
#firstComponent,
#thirdComponent,
#webPages {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.socialLinks {
  cursor: pointer;
}
.socialLinks:hover {
  background-color: white;
}

@media (max-width: 765px) {
  #footerComponent .container {
    display: block;
    height: auto;
    text-align: center;
  }
  #logoImage {
    width: 170px;
    margin-bottom: 20px;
  }
  #firstComponent {
    align-items: center;
  }
  #getInvite {
    justify-content: center;
    margin-bottom: 20px;
  }
  #copyRight {
    width: 100%;
  }
  #features {
    margin: 15px 0;
  }
  #home {
    margin-bottom: 15px;
  }
  #webPages {
    margin: 20px 0;
  }
  #footerComponent {
    height: 350px;
  }
}
