#featuresIntro {
  height: 490px;
  display: flex;
  background-color: black;
  color: white;
  flex-direction: row-reverse;
}
#featuresIntro span {
  width: 610px;
  display: flex;
  align-items: center;
  justify-content: center;
}
#featuresIntro img {
  width: 63%;
}
#featuresComponent {
  width: 387px;
}
#featuresComponent h2 {
  font-weight: bold;
  font-size: 40px;
  line-height: 48px;
  letter-spacing: 4.16667px;
  text-transform: uppercase;
}
#featuresComponent div {
  font-weight: normal;
  font-size: 15px;
  line-height: 25px;
  mix-blend-mode: normal;
  opacity: 0.6;
  width: 387px;
  height: 75px;
  margin-top: 21px;
}
#featuresBody {
  display: grid;
  /* specifying the number of rows is not really important, we only need the number of columns */
  grid-template-columns: repeat(3, 1fr);
  height: 700px;
  margin: 165px auto;
}
#featuresInviteComp h2 {
  font-weight: bold;
  font-size: 40px;
  line-height: 48px;
  letter-spacing: 4.16667px;
  text-transform: uppercase;
  width: 400px;
}
#featuresInviteComp span:hover {
  text-decoration: underline;
}
#featuresInviteComp span {
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  text-align: right;
  letter-spacing: 2px;
  margin-right: 14px;
  cursor: pointer;
}
#featuresInviteComp .container {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 1097px) {
  #featuresIntro img {
    width: 600px;
  }
  #featuresBody {
    grid-template-columns: repeat(2, 1fr);
    height: 1100px;
  }
  /* #three,
  #four {
    top: 100px;
  }
  #five,
  #six {
    top: 200px;
  } */
}

@media (max-width: 987px) {
  #featuresIntro img {
    content: url("../assets/features/tablet/hero.jpg");
  }
  #featuresIntro span {
    padding: 0 40px;
  }
}

@media (max-width: 740px) {
  #featuresIntro {
    display: block;
    height: 600px;
  }
  #featuresIntro img {
    content: url("../assets/features/mobile/hero.jpg");
    width: 100%;
    height: 350px;
  }
  #featuresIntro span {
    width: 100%;
  }
  #featuresComponent {
    margin-top: 50px;
  }
  #featuresBody {
    display: block;
    height: 2000px;
  }
  #two {
    top: 50px;
  }
  #three {
    top: 100px;
  }
  #four {
    top: 200px;
  }
  #five {
    top: 250px;
  }
  #six {
    top: 300px;
  }
  #featuresInviteComp .container {
    display: block;
    text-align: center;
  }
  #featuresInviteComp .container h2 {
    width: 100%;
    margin-bottom: 20px;
  }
  #featuresInviteComp .container div {
    justify-content: center;
  }
}

@media (max-width: 414px) {
  #featuresComponent div {
    width: 100%;
  }
}
