#MediumSizedComponent {
  width: 610px;
  height: 650px;
  display: flex;
  align-items: center;
  justify-content: center;
}
#mediumSizedHeading {
  font-weight: bold;
  font-size: 40px;
  line-height: 48px;
  letter-spacing: 4.16667px;
  text-transform: uppercase;
  width: 387px;
}
#mediumSizedBody {
  font-weight: normal;
  font-size: 15px;
  line-height: 25px;
  width: 400px;
  mix-blend-mode: normal;
  margin-top: 21px;
  margin-bottom: 48px;
}
#bottomTextMediumSizedComponenet {
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  margin-right: 14px;
  cursor: pointer;
}
#bottomTextMediumSizedComponenet:hover {
  text-decoration: underline;
}
#arrowContainer {
  display: flex;
  align-items: center;
}
#meduimSizedContentBody {
  width: 387px;
}

/* tablet size */
@media (max-width: 1001px) {
  #mediumSizedBody {
    width: 383px;
  }
  #MediumSizedComponent {
    padding-left: 40px;
    padding-right: 40px;
  }
}

/* mobile size */
@media (max-width: 765px) {
  #MediumSizedComponent {
    padding-left: 0;
    padding-right: 0;
    width: 100%;
  }
  #mediumSizedHeading {
    font-size: 32px;
    line-height: 40px;
    letter-spacing: 3.33333px;
  }
  #mediumSizedBody,
  #meduimSizedContentBody,
  #mediumSizedHeading {
    width: 100%;
  }
  #meduimSizedContentBody {
    padding: 0 10px;
  }
}
