.navbar {
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
}
.navbar-brand {
  cursor: pointer;
}

#navbarNavAltMarkup {
  justify-content: space-evenly;
}
.nav-link {
  color: black;
}
.nav-link:hover {
  color: black;
  opacity: 0.3;
}

#inviteButton {
  width: 158px;
  height: 40px;
  color: #ffffff;
  background-color: black;
  border: 0;
}
#inviteButton:hover {
  background-color: lightgray;
  color: black;
}

@media (max-width: 765px) {
  .navbar {
    text-align: center;
  }
}
