#Pro,
#Business,
#Basic {
  text-align: center;
  width: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
#Basic,
#Business {
  height: 407px;
  background-color: #f5f5f5;
  color: black;
}
#Pro {
  height: 470px;
  background-color: black;
  color: white;
}
#Basic button,
#Business button {
  width: 270px;
  height: 40px;
  background-color: black;
  color: white;
  border: 0;
}
#Basic button:hover,
#Business button:hover {
  background-color: #dfdfdf;
  color: black;
}
#Pro button:hover {
  background-color: #f5f5f5;
}
#Basic .cardHeading,
#Business .cardHeading {
  margin-top: 56px;
}

#Pro .cardHeading {
  margin-top: 88px;
}

#Pro button {
  width: 270px;
  height: 40px;
  background-color: white;
  color: black;
  border: 0;
}
.cardHeading {
  font-weight: bold;
  font-size: 24px;
  line-height: 25px;
}
.cardText {
  font-weight: normal;
  font-size: 15px;
  line-height: 25px;
  mix-blend-mode: normal;
  opacity: 0.6;
  width: 270px;
  height: 75px;
  margin-top: 18px;
  margin-bottom: 40px;
}
.cardAmount {
  font-weight: bold;
  font-size: 40px;
  line-height: 48px;
  letter-spacing: 4.16667px;
  text-transform: uppercase;
}
.cardDate {
  font-weight: normal;
  font-size: 15px;
  line-height: 25px;
  text-align: center;
  margin-bottom: 40px;
}
.button {
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

@media (max-width: 987px) {
  #Pro {
    margin: 30px 0;
  }
}
