#homePageBody {
  padding: 0;
}
.homePageTopBodycontentBox {
  display: flex;
}
#homePageMiddleBody {
  display: flex;
}
#homePageFeatures {
  display: flex;
  margin: 165px auto;
  justify-content: space-between;
}

/* tablet size */
@media (max-width: 1001px) {
  #homePageMiddleBody {
    flex-wrap: wrap;
  }
  #homePageFeatures {
    display: block;
  }
}

/* mobile size */
@media (max-width: 765px) {
  #content1,
  #content3 {
    flex-direction: column-reverse;
  }
  #content2 {
    display: block;
  }
  #homePageFeatures {
    padding-top: 100px;
  }
}
