.homePageFeaturesComp {
  width: 350px;
  height: 218px;
  position: relative;
}
.homePageFeaturesComp h3 {
  font-weight: bold;
  font-size: 18px;
  line-height: 25px;
  text-align: center;
  position: absolute;
  top: 120px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
}
.homePageFeaturesComp div {
  font-weight: normal;
  font-size: 15px;
  line-height: 25px;
  mix-blend-mode: normal;
  opacity: 0.6;
  text-align: center;
  position: absolute;
  top: 200px;
  left: 50%;
  transform: translateX(-50%);
  width: 350px;
  height: 75px;
}
.homePageFeaturesComp img {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

@media (max-width: 1001px) {
  .homePageFeaturesComp {
    width: 100%;
    margin-bottom: 70px;
  }
  #homePageFeaturesComp div {
    top: 160px;
  }
}
@media (max-width: 414px) {
  .homePageFeaturesComp div {
    width: 100%;
  }
}
